.project {
  padding-bottom: 45px;

  &__container {}

  &__inner {
    display: flex;
    margin-bottom: 40px;
    background-color: #F7FAFC;

    @include mobile {
      flex-wrap: wrap;
    }
  }

  &__right {
    align-self: center;
    padding: 20px 65px;

    @include tablet {
      padding: 20px 40px;
    }

    @include small-tablet {
      padding: 20px;
    }
  }

  &__title {
    margin: 0;
    margin-bottom: 25px;
    font-weight: 700;
    font-size: 2.5rem;
    line-height: 120%;
    color: var(--dark-color);

    @include small-tablet {
      font-size: 2rem;
    }
  }

  &__descr {
    margin: 0;
    margin-bottom: 30px;
    font-size: 1rem;
    line-height: 118%;
    color: var(--riverBed-color);
  }

  &__bottom {
    .bottom__date {
      font-size: 16px;
    }

    .btn-more {
      font-size: 16px;
    }
  }

  &__image {
    flex-shrink: 0;
    width: 41%;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      width: 100%;
    }
  }

  &__see-more {
    display: flex;
    justify-content: flex-end;
  }
}

.see-more {
  display: flex;
  align-items: center;
  padding: 10px;
  border-radius: 5px;
  width: 122px;
  font-weight: 700;
  font-size: 1rem;
  color: var(--light-color);
  background-color: var(--blue-color);
  transition: background-color 0.2s ease-in-out;

  &:hover {
    background-color: #4e1ebc;
  }

  span {
    display: inline-block;
    margin-right: 9px;
  }
}
