.details-content {
  padding-top: 100px;
  background-color: var(--light-color);

  &__container {
    position: relative;
    padding-bottom: 60px;
  }

  &__top {
    margin-bottom: 40px;
  }

  &__image {
    width: 100%;
    height: 380px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }

    @include mobile {
      height: 200px;
    }
  }

  &__bottom {
    max-width: 1015px;
    margin: 0 auto;
  }

  &__title {
    margin-bottom: 20px;
    font-size: 2.8rem;
    line-height: 120%;

    @include mobile {
      font-size: 2rem;
    }
  }

  &__subtitle {
    position: relative;
    margin: 0;
    margin-bottom: 35px;
    padding-bottom: 30px;
    font-weight: 700;
    font-size: 1.12rem;
    line-height: 122%;
    color: #2D3748;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: #E8E7E7;
    }

    @include mobile {
      font-size: 0.9rem;
    }
  }

  &__date {
    margin-left: 20px;
    font-weight: 400;
    color: var(--riverBed-color);
  }

  &__text {
    h2 {
      margin: 0;
      font-weight: 700;
      color: var(--oxfordBlue-color);
    }

    p {
      margin: 0;
      font-weight: 400;
      font-size: 1rem;
      line-height: 118%;
      color: var(--riverBed-color);
    }
  }
}

.details-content__text>p:nth-child(1) {
  margin-bottom: 20px;
}

.details-content__text>p:nth-child(2) {
  margin-bottom: 20px;
}

.details-content__text>p:nth-child(4) {
  margin-bottom: 30px;
}

.details-content__text>p:nth-child(6) {
  margin-bottom: 30px;
}

.details-content__text>h2:nth-child(3) {
  margin-bottom: 20px;
  font-style: italic;
  font-size: 1rem;
}

.details-content__text>h2:nth-child(5) {
  margin-bottom: 30px;
  font-size: 30px;
}

.details-content__text>h2:nth-child(7) {
  margin-bottom: 30px;
  font-size: 30px;
}
