.header {
  position: absolute;
  left: 0;
  top: 0;
  z-index: 100;
  width: 100%;
  background-color: var(--light-color);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 36px;
    padding-bottom: 25px;
  }

  &__logo {
    font-weight: 700;
    font-size: 1.25rem;
    color: var(--dark-color);
  }

  &__search {}

  &__right {
    position: relative;
    display: flex;
    align-items: center;

    @include small-tablet {
      position: fixed;
      top: 0;
      left: -150vw;
      display: flex;
      flex-direction: column;
      align-items: center;
      overflow-y: auto;
      padding: 20% 0;
      width: 80%;
      height: 100vh;
      background-color: #ccc;
      transition: left 0.5s ease-in-out;

      .nav {
        margin-right: 0;
      }

      .nav__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-bottom: 100px;
      }

      .nav__item:not(:last-child) {
        margin-right: 0;
        margin-bottom: 50px;
      }

      .nav__link {
        font-weight: 700;
        font-size: 40px;

        @include mobile {
          font-size: 35px;
        }
      }

      .nav-close {
        position: absolute;
        top: 15px;
        right: 15px;
        width: 18px;
        height: 18px;

        svg {
          display: block;
          width: 100%;
          height: 100%;
        }
      }

      .social {
        margin-top: auto;
      }
    }

    &--visible {
      left: 0;
    }
  }

  &__nav {}

  &__social {}

  &--details {
    .header__container {
      padding-bottom: 40px;
    }
  }
}

.nav-close {
  display: none;

  @include small-tablet {
    display: block;
  }
}

.burger {
  display: none;
  @include burger;

  @include small-tablet {
    display: block;
  }
}

.search-form {
  &__btn {
    display: flex;
    align-items: center;
    font-size: 1rem;
    color: var(--riverBed-color);
    transition: color 0.2s ease-in-out;

    &:hover {
      color: var(--dark-color);
    }

    svg {
      display: block;
      margin-right: 12px;
      width: 20px;
      height: 20px;
    }
  }
}

.nav {
  margin-right: 30px;

  &__list {
    display: flex;
    align-items: center;
  }

  &__item {
    &:not(:last-child) {
      margin-right: 30px;
    }
  }

  &__link {
    position: relative;
    font-size: 1rem;
    color: var(--dark-color);

    &::before {
      content: '';
      position: absolute;
      left: 0;
      bottom: 0;
      width: 100%;
      height: 1px;
      background-color: currentColor;
      transform-origin: left;
      transform: scaleX(0);
      transition: all 0.2s ease-in-out;

      @include tablet {
        display: none;
      }
    }

    &:hover {
      &::before {
        transform: scaleX(1);
      }
    }
  }
}

.social {
  display: flex;
  align-items: center;

  &__item {
    &:not(:last-child) {
      margin-right: 35px;
    }
  }

  &__link {
    display: inline-block;
    width: 24px;
    height: 24px;


    svg {
      display: block;
      width: 100%;
      height: 100%;
      transition: fill 0.2s ease-in-out;

      @include for-desktop {
        &:hover {
          fill: var(--blue-color);
        }
      }
    }
  }
}
