.footer {
  border-top: 1px solid #e8e8e8;
  background-color: var(--light-color);

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 30px;
    padding-bottom: 30px;
  }

  &__copyright {
    padding-right: 15px;
    line-height: 118%;

    span {
      font-weight: 700;
      font-size: 1rem;
    }
  }

  &__social {}

  &--details {
    background-color: #e5e5e5;
  }
}
