.contents {
  background-color: var(--light-color);

  &__container {
    padding-top: 40px;
    padding-bottom: 40px;
  }

  &__top {
    margin-bottom: 45px;
  }

  &__bottom {}

  &__columns {
    display: flex;
    margin: 0 -20px;

    @include tablet {
      margin: 0 -10px;
    }

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__column {
    flex: 1 1 33.333%;
    padding: 0 20px;
    height: auto;

    @include tablet {
      padding: 0 10px;
    }

    @include small-tablet {
      flex: 1 1 100%;
    }

    &:not(:last-child) {
      @include mobile {
        margin-bottom: 30px;
      }
    }
  }

}

.top-content {
  display: flex;
  justify-content: space-between;
  border-radius: 5px;
  border: 1px solid #E2E2E2;
  box-shadow: 0px 5px 4px rgba(221, 221, 221, 0.25);

  &__right {
    padding: 40px 0 40px 55px;

    @include tablet {
      padding: 20px;
    }
  }

  &__image {
    max-width: 433px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__title {
    margin-bottom: 27px;
    font-size: 1.87rem;
    line-height: 120%;
  }

  &__text {
    margin: 0;
    margin-bottom: 30px;
    max-width: 403px;
    font-size: 0.87rem;
    line-height: 121%;
    color: var(--riverBed-color);
  }

  @include mobile {
    flex-wrap: wrap;
  }
}

.bottom {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 14px;

  &__date {
    font-size: 0.75rem;
    color: var(--riverBed-color);
  }

  &__btn {}

  &__image {
    max-width: 433px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
}

.btn-more {
  font-weight: 700;
  font-size: 0.75rem;
  color: var(--oxfordBlue-color);
  transition: all 0.2s ease-in-out;

  @include for-desktop {
    &:hover {
      font-size: 14px;
      color: var(--dark-color);
    }
  }
}

.card {
  position: relative;
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  height: 100%;
  background-color: var(--light-color);
  box-shadow: 0px 4px 4px rgba(192, 189, 189, 0.25);
  transition: all 0.3s ease-in-out;

  &:hover {
    transform: translateY(5px);
  }

  &__image {
    height: 179px;

    img {
      display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  &__inner {
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    padding: 20px 20px 25px 20px;
  }

  &__title {
    margin-bottom: 15px;
    font-size: 1.25rem;
    line-height: 120%;
  }

  &__descr {
    margin: 0;
    margin-bottom: 20px;
    color: var(--riverBed-color);
  }

  &__bottom {
    margin-top: auto;

    .btn-more {
      &::before {
        content: "";
        position: absolute;
        inset: 0;
        width: 100%;
        height: 100%;
      }
    }
  }
}
