.hero {
  height: 100vh;
  min-height: 500px;
  background-color: #FBFBFB;

  &__container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding-top: 155px;
    padding-bottom: 75px;

    @include small-tablet {
      flex-wrap: wrap;
    }
  }

  &__content {
    margin-left: 55px;

    @include tablet {
      margin-left: 0;
      width: 65%;
    }

    @include small-tablet {
      margin-bottom: 30px;
      width: 100%;
    }
  }

  &__title {
    position: relative;
    margin: 0;
    margin-bottom: 20px;
    max-width: 354px;
    font-weight: 700;
    font-size: 3.75rem;
    line-height: 121%;
    color: var(--dark-color);

    &::after {
      content: "";
      position: absolute;
      right: 90px;
      bottom: 0;
      width: 70px;
      height: 67px;
      background-image: url('../../img/coffee.png');
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;

      @include small-tablet {
        right: 170px;
        bottom: -60px;
      }

      @include mobile {
        display: none;
      }
    }

    @include tablet {
      font-size: 3.2rem;
    }

    @include small-tablet {
      max-width: 100%;
    }

    @include mobile {
      font-size: 2.8rem;
    }
  }

  &__descr {
    margin: 0;
    font-size: 1.87rem;
    line-height: 120%;
    color: #4A5568;

    @include tablet {
      font-size: 1.7rem;
    }

    @include mobile {
      font-size: 1.4rem;
    }
  }

  &__image {
    width: 476px;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }

    @include small-tablet {
      width: 70%;
    }

    @include mobile {
      width: 100%;
    }
  }

  @include small-tablet {
    height: auto;
  }
}
